import { fetchEndpoint } from '../server/network'

// 商品分类
export const GoodCategory = () => fetchEndpoint('/admin/api.category.list', 'POST', {})

// 商品品牌
export const GoodBrand = () => fetchEndpoint('/admin/api.brand.list', 'POST', {})

// 评价列表
export const EvaluationList = params =>
  fetchEndpoint('/admin/api.comment.list', 'POST', {
    user_id: params.user_id,
    order_sn: params.order_sn,
    suppliers_name: params.suppliers_name,
    cat_id: params.cat_id[0],
    brand_id: params.brand_id,
    answer_is: params.answer_is,
    hidden_is: params.hidden_is,
    comment_rank: params.comment_rank,
    goods_sn: params.goods_sn,
    comment_start: Number(params.comment_start / 1000),
    comment_end: Number(params.comment_end / 1000),
    mobile: params.mobile,
    page: params.page,
    page_size: params.page_size
  })

// 回复评价
export const ResponseEvaluation = params =>
  fetchEndpoint('/admin/api.comment.answer', 'POST', {
    comment_id: params.comment_id,
    answer: params.answer
  })

// 隐藏回复
export const HidEvaluation = id =>
  fetchEndpoint('/admin/api.comment.hide', 'POST', {
    comment_id: id
  })

// 显示回复
export const ShowEvaluation = id =>
  fetchEndpoint('/admin/api.comment.show', 'POST', {
    comment_id: id
  })

// 评价置顶
export const SetTop = params =>
  fetchEndpoint('/admin/api.comment.top', 'POST', {
    comment_id: params.id,
    top_is: params.top_is
  })

// 评价详情
export const EvaluationDetail = id =>
  fetchEndpoint('/admin/api.comment.info', 'POST', {
    comment_id: id
  })
