import { fetchEndpoint, fetchEndpointBlob } from '../server/network'

// 获取账户汇款信息
export const getRemitInfo = params =>
  fetchEndpoint('/admin/api.remit.settings.get', 'POST', {
    suppliers_id: params
  })

/**
 * @DateTime 2019-07-12
 * @description  [账户汇款信息设置]
 * @param    {[string]} pay_company [收款主体]
 * @param    {[string]} payee [收款主体]
 * @param    {[string]} remit_account [收款账号]
 * @param    {[string]} bank [收款银行（无则为空）]
 * @param    {[float]} money_service_percent [现金结算佣金占比]
 * @param    {[float]} huanbi_service_percent [积分结算佣金]
 * @param    {[float]} money_income_percent [现金收入手续费]
 * @param    {[float]} huanbi_income_percent [积分收入手续费]
 * @param    {[float]} money_remit_percent [现金打款手续费]
 * @param    {[float]} huanbi_remit_percent [积分汇款手续费]
 * @param    {[int]} remit_type [汇款类型 1：支付宝 2：银行]
 */
export const setRemitInfo = params =>
  fetchEndpoint('/admin/api.remit.settings.set', 'POST', {
    ...params
  })

// 创建汇总单
export const createStatement = params =>
  fetchEndpoint('/admin/api.multi.statement.create', 'POST', {
    ...params
  })

// 编辑汇总单
export const updateStatement = params =>
  fetchEndpoint('/admin/api.multi.statement.update', 'POST', {
    ...params
  })

// 获取操作角色
export const getRole = () => fetchEndpoint('/admin/api.suppliers.role.get', 'POST', {})

/**
 * @DateTime 2019-07-12
 * @description  [获取汇总单详情]
 * @param    {[string]} multi_statement_id [汇总单ID]
 */
export const getStatementInfo = multi_statement_id =>
  fetchEndpoint('/admin/api.multi.statement.get', 'POST', {
    multi_statement_id: multi_statement_id
  })

// 财务确认打款
export const confirmRemit = (multi_statement_id, remit_log) =>
  fetchEndpoint('/admin/api.multi.statement.remit', 'POST', {
    multi_statement_id: multi_statement_id,
    remit_log: remit_log
  })

// 汇总单列表
export const getSettlementList = params =>
  fetchEndpoint('/admin/api.multi.statement.list.get', 'POST', {
    ...params
  })

// 商家确认汇总单
export const confirmSettlement = params =>
  fetchEndpoint('/admin/api.multi.statement.suppliers.confirm', 'POST', {
    ...params
  })

// 平台扣减商家保证金
export const bondReduce = (suppliers_id, bond, direction, remark) =>
  fetchEndpoint('/admin/api.suppliers.bond.reduce', 'POST', {
    suppliers_id: suppliers_id,
    bond: bond,
    direction: direction,
    remark: remark
  })

// 打印结算单(此接口参数淘汰)
export const exportSettleList = id =>
  fetchEndpointBlob('/admin/api.multi.statement.print', 'POST', {
    multi_statement_id: id
  })

// 保证金流水
export const getBondList = (id, page, size = 10) =>
  fetchEndpoint('/admin/api.suppliers.bond.list', 'POST', {
    suppliers_id: id,
    page: page,
    size: size
  })

