<template>
  <div class="container">
    <!-- header -->
    <page-title first-title="评价列表"></page-title>
    <!-- search -->
    <div class="list-search-wrapper">
      <!-- search content -->
      <el-form :inline="true" label-width="120px">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="用户ID">
              <el-input clearable v-model="search_infos.user_id"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="订单编号">
              <el-input clearable v-model="search_infos.order_sn"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="商家名称" v-if="isSuppliers !== 'supplier'">
              <el-input clearable v-model="search_infos.suppliers_name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="商品分类">
              <el-cascader
                :options="good_category"
                :props="props"
                :show-all-levels="false"
                v-model="search_infos.cat_id"
                clearable
              ></el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="商品品牌">
              <el-select clearable filterable v-model="search_infos.brand_id">
                <el-option
                  v-for="brand in brand_lists"
                  :key="brand.brand_id"
                  :label="brand.brand_name"
                  :value="brand.brand_id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="回复状态">
              <el-select clearable v-model="search_infos.answer_is">
                <el-option
                  v-for="status in res_status"
                  :key="status.id"
                  :label="status.name"
                  :value="status.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="评价显示状态">
              <el-select clearable v-model="search_infos.hidden_is">
                <el-option
                  v-for="status in hidden_status"
                  :key="status.id"
                  :label="status.name"
                  :value="status.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="评价类型">
              <el-select clearable v-model="search_infos.comment_rank">
                <el-option
                  v-for="type in evaluation_type"
                  :key="type.id"
                  :label="type.name"
                  :value="type.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="商品编号">
              <el-input clearable v-model="search_infos.goods_sn"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="评论开始时间">
              <el-date-picker
                v-model="search_infos.comment_start"
                type="datetime"
                placeholder="选择开始时间"
                value-format="timestamp"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="评论结束时间">
              <el-date-picker
                v-model="search_infos.comment_end"
                type="datetime"
                placeholder="选择结束时间"
                value-format="timestamp"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="手机号">
              <el-input clearable v-model="search_infos.mobile"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <!-- search button -->
      <el-button-group>
        <el-button type="primary" icon="el-icon-search" @click="getList">查询</el-button>
        <el-button type="primary" icon="el-icon-refresh" @click="clearSearch">重置</el-button>
        <el-button type="primary" @click="hiddenSome">批量隐藏</el-button>
      </el-button-group>
    </div>
    <!-- list -->
    <div class="list-content-wrapper">
      <el-table
        :data="evaluation_lists"
        :header-cell-style="{ background: '#eef1f6' }"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="订单编号" prop="order_sn" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column
          label="商家名称"
          prop="suppliers_name"
          :show-overflow-tooltip="true"
          v-if="isSuppliers !== 'supplier'"
        ></el-table-column>
        <el-table-column label="用户ID" prop="user_id" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="用户名" prop="consignee" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="手机号" prop="mobile" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="商品ID" prop="goods_id" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="商品名称" prop="goods_name" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="评论时间" prop="created_at" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="评价类型" prop="comment_rank" :formatter="fmtType"></el-table-column>
        <el-table-column label="评价图片">
          <template slot-scope="scope">
            <img :v-show="scope.row.img.length > 0" :src="scope.row.img[0]" width="50" height="50" />
          </template>
        </el-table-column>
        <el-table-column label="评价内容" prop="content" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="回复状态" prop="answer_is" :formatter="fmtResStatus"></el-table-column>
        <el-table-column label="是否置顶">
          <template slot-scope="scope">
            <el-switch
              :value="!!scope.row.top_is"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="setTopEvaluation($event, scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="250px">
          <template slot-scope="scope">
            <el-button type="text" @click="goDetail(scope.row.comment_id)">查看</el-button>
            <el-button type="text" @click="goRes(scope.row)" :disabled="!!scope.row.answer_is">回复</el-button>
            <el-button type="text" @click="goHidden(scope.row)" :disabled="!!scope.row.hidden_is">{{
              !!scope.row.hidden_is ? '已隐藏' : '隐藏'
            }}</el-button>
            <el-button type="text" @click="goShow(scope.row)" :disabled="!scope.row.hidden_is">{{
              !scope.row.hidden_is ? '已取消隐藏' : '取消隐藏'
            }}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- pagination -->
    <div class="list-pagination-wrapper">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        background
        layout="prev, pager, next, jumper"
        :total="total"
        :hide-on-single-page="true"
      >
      </el-pagination>
    </div>
    <!-- res dialog -->
    <div class="error-good-info-wrapper">
      <el-dialog :visible.sync="showDialog" width="50%" :show-close="false">
        <div slot="title">回复评论</div>
        <div>
          <el-input type="textarea" v-model="res_content"></el-input>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitRes">提交回复</el-button>
          <el-button type="primary" @click="showDialog = false">取 消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { RESPONSE_STATUS, HIDDEN_STATUS, EVALUATION_TYPE } from './static'
import {
  EvaluationList,
  HidEvaluation,
  ShowEvaluation,
  SetTop,
  ResponseEvaluation,
  GoodCategory,
  GoodBrand
} from '../../api/evaluation'
import { getRole } from '../../api/settle'
export default {
  name: 'EvaluationList',
  data() {
    return {
      // 查询下拉框状态
      res_status: RESPONSE_STATUS,
      hidden_status: HIDDEN_STATUS,
      evaluation_type: EVALUATION_TYPE,
      good_category: [],
      good_brand: [],
      // 查询信息
      search_infos: {
        suppliers_id: '',
        user_id: '',
        order_sn: '',
        suppliers_name: '',
        cat_id: '',
        brand_id: '',
        answer_is: '',
        hidden_is: '',
        comment_rank: '',
        goods_sn: '',
        comment_start: '',
        comment_end: '',
        mobile: '',
        page: 1,
        page_size: 10
      },
      // 分页
      total: 0,
      currentPage: 1,
      // 列表
      evaluation_lists: [],
      category_lists: [],
      brand_lists: [],
      // 商品分类级联
      props: {
        checkStrictly: true,
        value: 'id',
        label: 'name',
        children: 'children'
      },
      // flag
      showDialog: false,
      isSuppliers: '',
      res_content: '', // 回复内容
      cur_comment: '', // 评价id
      multiSelectEvaluation: [] // 多选评价
    }
  },
  created() {
    this.getList()
    GoodBrand().then(res => {
      this.brand_lists = res
    })
    GoodCategory().then(res => {
      this.good_category = res
    })
    getRole().then(res => {
      this.isSuppliers = res.role
    })
  },
  computed: {
    res_params() {
      let obj = {}
      obj.answer = this.res_content
      obj.comment_id = this.cur_comment.comment_id
      return obj
    }
  },
  methods: {
    handleSelectionChange(val) {
      this.multiSelectEvaluation = val.map(function(item) {
        return item.comment_id
      })
    },
    handleCurrentChange() {
      this.search_infos.page = this.currentPage
      this.getList()
    },
    getList() {
      console.log(this.search_infos.cat_id)
      EvaluationList(this.search_infos)
        .then(res => {
          this.evaluation_lists = res.list
          this.total = res.record_count
        })
        .catch(err => {
          this.$message.error(err.errorMsg)
        })
    },
    clearSearch() {
      let _s = this.search_infos
      for (let key in this.search_infos) {
        _s[key] = ''
      }
      _s.page = 1
      _s.page_size = 10
    },
    hiddenSome() {
      HidEvaluation(this.multiSelectEvaluation).then(res => {
        console.log(res)
        this.$message.success('批量隐藏成功')
        this.getList()
      })
    },
    // 操作中的方法
    goDetail(id) {
      this.$router.push({ name: 'EvaluationDetail', params: { id: id } })
    },
    goRes(row) {
      this.showDialog = true
      this.res_content = ''
      this.cur_comment = row
    },
    goHidden(row) {
      this.$confirm('确定隐藏该条评价吗', '提示').then(() => {
        HidEvaluation([row.comment_id]).then(res => {
          if (res) {
            this.$message.success('隐藏成功')
            row.hidden_is = true
          }
        })
      })
    },
    goShow(row) {
      this.$confirm('确定显示该条评价吗', '提示').then(() => {
        ShowEvaluation([row.comment_id]).then(res => {
          if (res) {
            this.$message.success('显示成功')
            row.hidden_is = false
          }
        })
      })
    },
    setTopEvaluation(value, row) {
      row.top_is = Number(value)
      SetTop({ id: row.comment_id, top_is: Number(value) }).then(res => {
        if (res) {
          let info = value ? '置顶成功' : '取消置顶成功'
          this.$message.success(info)
        }
      })
    },
    submitRes() {
      if (this.res_content === '') {
        this.$message.error('回复消息非空')
        return
      } else {
        ResponseEvaluation(this.res_params).then(res => {
          console.log(res)
          // this.cur_comment.answer_content = true // 为了让回复按钮置灰
          this.cur_comment.answer_is = 1
          this.$message.success('回复成功')
          this.showDialog = false
        })
      }
    },
    fmtResStatus(row, column, cellValue) {
      return this.utils.formatStatus(cellValue, this.res_status)
    },
    fmtType(row, column, cellValue) {
      return this.utils.formatStatus(cellValue, this.evaluation_type)
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  & > div {
    margin-bottom: 15px;
  }
  .list-pagination-wrapper {
    text-align: center;
  }
}
</style>

<style lang="scss">
.error-good-info-wrapper {
  .el-dialog__header {
    background: #505763;
    text-align: left;
    padding: 5px 20px;
    color: #fff;
    height: 26px;
    line-height: 26px;
    font-size: 14px;
  }
  .el-dialog__body {
    text-align: center;
    .fail_info {
      text-align: left;
    }
  }
  .el-dialog__footer {
    background: #e4e4e4;
    text-align: center;
    padding: 5px 20px;
    .el-button {
      padding: 5px 32px;
    }
  }
  label {
    color: #f00;
  }
}
</style>
