<template>
  <div class="container" v-loading.fullscreen.lock="loading">
    <!-- header -->
    <page-title first-title="评价详情"></page-title>
    <!-- infos -->
    <div class="evaluation-infos-wrapper">
      <!-- consignee info -->
      <div class="consignee-info-wrapper">
        <h2>收货人信息</h2>
        <el-table :data="consignee_info" border style="width: 50%;" :header-cell-style="{ background: '#eef1f6' }">
          <el-table-column label="收货人" prop="name"></el-table-column>
          <el-table-column label="手机号" prop="mobile"></el-table-column>
        </el-table>
      </div>
      <!-- order info -->
      <div class="order-info-wrapper">
        <h2>订单信息</h2>
        <el-table :data="order_info" border :header-cell-style="{ background: '#eef1f6' }">
          <el-table-column label="订单号" prop="order_sn" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="下单时间" prop="add_time" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="付款时间" prop="pay_time" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="支付方式" prop="pay_name"></el-table-column>
          <el-table-column label="发货时间" prop="shipping_time" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="发货单号" prop="invoice_no" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="支付总金额" prop="goods_amount"></el-table-column>
          <el-table-column label="现金金额" prop="money_paid"></el-table-column>
					<el-table-column label="积分抵扣" prop="surplus"></el-table-column>
        </el-table>
      </div>
      <!-- good info -->
      <div class="good-info-wrapper">
        <h2>商品信息</h2>
        <el-table :data="good_info" border :header-cell-style="{ background: '#eef1f6' }">
          <el-table-column label="商品名称" prop="goods_name" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="所属商品分类" prop="cat_name" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="所属商品品牌" prop="brand_name" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="商品编号" prop="goods_sn" :show-overflow-tooltip="true"></el-table-column>
        </el-table>
      </div>
      <!-- buyer evaluation -->
      <div class="buyer-evaluation-wrapper">
        <h2>买家评论</h2>
        <el-table :data="buyer_evaluation" border :header-cell-style="{ background: '#eef1f6' }">
          <el-table-column width="80" label="商品评分" prop="goods_score"></el-table-column>
          <el-table-column width="80" label="物流评分" prop="shipping_score"></el-table-column>
          <el-table-column width="80" label="服务评分" prop="service_score"></el-table-column>
          <el-table-column width="80" label="评价类型" prop="grade_named"></el-table-column>
          <el-table-column label="评价时间" prop="created_at" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column class="over-style-wrapper" label="评价图片" width="300">
            <template slot-scope="scope">
              <div class="img-wrapper" v-if="scope.row.img && scope.row.img.length">
                <!-- <img v-for="(item, index) in scope.row.img" :key="index" :src="item" width="50" height="50" @click="goPreview(item)"/> -->
                <el-image
                  style="width: 50px; height: 50px"
                  v-for="(item, index) in scope.row.img"
                  :key="index"
                  :src="item"
                  :preview-src-list="srcList"
                >
                </el-image>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="评论内容" prop="content" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column label="追加评论" prop="append_content" :show-overflow-tooltip="true"></el-table-column>
        </el-table>
      </div>
      <!-- response textarea -->
      <div class="response-textarea-wrapper">
        <h2>回复评论</h2>
        <div>
          <span>评论内容：</span>
          <el-input type="textarea" v-model="answer.content"></el-input>
        </div>
        <div class="btn-group-wrapper">
          <el-button type="primary" @click="submitRes" :disabled="!!answer.answer_is">提交回复</el-button>
          <el-button type="primary" @click="goBack">取消</el-button>
        </div>
      </div>
      <!-- preview img -->
      <div class="preview-img-wrapper"></div>
    </div>
  </div>
</template>

<script>
import { EvaluationDetail, ResponseEvaluation } from '../../api/evaluation'
export default {
  name: 'EvaluationDetail',
  data() {
    return {
      consignee_info: [],
      order_info: [],
      good_info: [],
      buyer_evaluation: [],
      srcList: [], // 大图预览
      answer: {},
      loading: true
    }
  },
  created() {
    EvaluationDetail(this.$route.params.id)
      .then(res => {
        this.consignee_info.push(res.consignee)
        this.order_info.push(res.orders)
        this.good_info.push(res.goods)
        this.buyer_evaluation.push(res.comment)
        this.srcList = res.comment.img && res.comment.img.length ? res.comment.img : []
        this.answer = res.answer
        this.loading = false
      })
      .catch(err => {
        console.log(err)
        this.loading = false
        this.$message.error('查看详情有误')
      })
  },
  computed: {
    res_params() {
      let obj = {}
      obj.answer = this.answer.content
      obj.comment_id = this.$route.params.id
      return obj
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    submitRes() {
      if (!this.answer.content) {
        this.$message.error('回复内容非空')
        return
      }
      ResponseEvaluation(this.res_params).then(res => {
        console.log(res)
        this.$message.success('回复成功')
        this.goBack()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.response-textarea-wrapper {
  & > div {
    display: flex;
    span {
      display: inline-flex;
      width: 70px;
    }
  }
  & > div.btn-group-wrapper {
    justify-content: center;
    margin-top: 15px;
  }
}
</style>

<style lang="scss">
.el-table_4_column_21 {
  & > div.cell {
    overflow-x: auto;
    white-space: nowrap;
    overflow-y: hidden;
    div.img-wrapper {
      height: 50px;
      /* img {
				margin-left: 10px;
			}
			img:first-child {
				margin-left: 5px;
			}
			img:last-child {
				margin-right: 5px;
			} */
    }
  }
}
</style>
